#SideNavBar{
    width: 280px;
    display: flex;
    flex-direction: column;
}

#sideNavBarTop{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

#sideNavBarTopText{
    font-size: 17px;
    font-weight: bold;
    color: #0f1419;
    width: 192px;
    margin-left: 10px;
    font-family: 'Poppins';
}

#sideNavBarTopImageContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    margin-right: 10px;
}

#sideNavBarTopImageContainer img{
    width: 20px;
    height: 20px;
}